import { Box } from '@mui/material'
import { useEffect, useRef, useState } from 'react'

function ClickOutsideGuard({
  onEdit,
  children,
  onClickOutside,
  onOutsideHold,
  onSnackbarChange,
}) {
  const [onDown, setOnDown] = useState(null)
  const [onUp, setOnUp] = useState(null)
  const wrapperRef = useRef(null)

  useEffect(() => {
    function handleClickOutside(e) {
      if (!onEdit) return

      const clickedInsideSelectMenu = Boolean(e.target.closest('.MuiList-root'))
      const clickedOnSelectMenu = Boolean(
        e.target.classList.contains('MuiBackdrop-root')
      )

      if (
        wrapperRef.current &&
        !wrapperRef.current.contains(e.target) &&
        !clickedOnSelectMenu &&
        !clickedInsideSelectMenu
      ) {
        onClickOutside()
        document.activeElement.blur()
      }
    }

    function handleHoldOutside(e) {
      if (wrapperRef.current && !wrapperRef.current.contains(e.target))
        onOutsideHold()
    }

    const handleMouseDown = (event) => {
      setOnDown(event.target)
    }

    const handleMouseUp = (event) => {
      if (!onEdit) return
      console.log(onDown)
      onSnackbarChange()
    }

    function handleSnackbarOpen(e) {
      if (!onEdit) return

      if (wrapperRef.current && !wrapperRef.current.contains(e.target)) {
        onSnackbarChange()
        document.activeElement.blur()
      }
    }

    // document.addEventListener('mousedown', handleClickOutside)
    document.addEventListener('mousedown', handleMouseDown)
    document.addEventListener('mouseup', handleMouseUp)
    document.addEventListener('touchstart', handleClickOutside)
    document.addEventListener('mousemove', handleHoldOutside)

    return () => {
      // document.removeEventListener('mousedown', handleClickOutside)
      document.removeEventListener('mousedown', handleMouseDown)
      document.removeEventListener('mouseup', handleMouseUp)
      document.removeEventListener('touchstart', handleClickOutside)
      document.removeEventListener('mousemove', handleHoldOutside)
    }
  }, [onClickOutside, onOutsideHold, onSnackbarChange, onEdit])

  return (
    <Box
      sx={{
        bgcolor: 'primary.opposite',
        webkitTouchCallout: 'none !important',
        webkitUserSelect: 'none !important',
      }}
      ref={wrapperRef}
    >
      {children}
    </Box>
  )
}
export default ClickOutsideGuard
